.newCollection{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:10px;
    
    margin-bottom: 100px;
}
.newCollection h1{
    color: #171717;
    font-size:50px;
    font-size: 600;

}
.newCollection hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background-color: #252525;
}
.NewCollectionData{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top :50px;
    gap:30px;
}